<template>
	<view class="accountSecurity">
		<view class="center-list-item" @tap='enterPage(0)' hover-class="bgColor">
			<text class="list-text">修改账号</text>
			<view class="">
				<text>{{phone}}</text>
				<uni-icons type="arrowright"></uni-icons>
			</view>
		</view>
		<view class="center-list-item" @tap='enterPage(1)' hover-class="bgColor">
			<text class="list-text">修改密码</text>
			<uni-icons type="arrowright"></uni-icons>
		</view>
		<view class="center-list-item" @tap='enterPage(2)' hover-class="bgColor">
			<text class="list-text">二级密码</text>
			<text>{{ifSetting}}</text>
			<uni-icons type="arrowright"></uni-icons>
		</view>
	</view>
</template>

<script>
	import {getInfor} from '@/utill/api/personalInfor/getInfor.js'
	export default {
		data() {
			return {
				phone:'',
				ifSetting: '未设置',
			}
		},
		methods: {
			enterPage(index){
				if(index==0){//修改账号
					uni.navigateTo({
						url:'/pages/AccoutSecurity/amendPhone?phone='+this.phone
					})
				}else if(index==1){//修改密码
					uni.navigateTo({
						url:'/pages/AccoutSecurity/amendPwd'
					})
				}else {//修改二级密码
                    uni.navigateTo({
                        url: '/pages/AccoutSecurity/secondPwd?phone='+this.phone
                    })
                }
			},
			getDocinfor(){
				getInfor({}).then(res=>{
					console.log(res)
					if(res.data.code==0){
						if(res.data.object.secondLevelPasswordSwitch){
							this.ifSetting='已启用'
						}else{
							this.ifSetting='未设置'
						}
					}else{
						uni.showToast({
							title:res.data.msg,
							icon:'none'
						})
					}
				}).catch(err=>{ 
					console.log(err)
				})
			}
		},
		onLoad(options) {
			this.phone=options.phone
			this.getDocinfor()
		}
	}
</script>

<style scoped>
	page{
		background-color: #FFFFFF;
	}
	.center-list-item {
		padding: 30rpx 30rpx;
		border-bottom: 1px solid #EEEEEE;
		align-items: center;
		justify-content: space-between;
		display: flex;
	}
	.list-text {
		font-size: 30rpx;
		color: #000000;
		font-family: PingFang-SC-Regular;
		flex: 1;
		text-align: left;
	}
	.bgColor{
		background-color: #F8F8F8;
	}
</style>
